.category-dropdown {
  border: 1px solid #b1abab;
  display: flex;
  flex-wrap: wrap;
  flex:1;
  min-width: 20em;
  position: relative;
  border-radius:6px;
  padding-left: 0.5em;
  box-shadow: 1px 1px 1px 0px  #888;
}

.category-label{
  /* border: 3px solid rgb(3, 39, 6); */
  display: flex;
  width: 100%;
  align-items: center;
}

.category-label :nth-child(1){
  color: rgb(172, 169, 169);
  font-size: 1em;
}

.category-label :nth-child(2){
  /* border: 3px solid rgb(3, 39, 6); */
  padding-left: 0.5em;
  flex:1;
  font-size: 1em;
}

.category-label :nth-child(2)
,.category-label :nth-child(3) :hover{
  cursor: pointer;
}

.category-label :nth-child(3){
  background-color: #fff;
  border: 0px;
  border-radius:6px;
}

.category-list {
  /* border: 1px solid red; */
  width: 100%;
  height: 100px;
  overflow: scroll;
  position: absolute;
  top:31px;
  left: 0;
  background-color: #f9f9f9;
  font-size: 1em;
  z-index: 2;
}

.category-list-item{
  padding-left: 0.5em;
  border-bottom: 1.5px solid #ddd;
}

.category-list-item:hover{
  background: rgb(192, 210, 235);
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}


